.App {
  text-align: center;
}
:root {
  --footercolor: black;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-link {
  color: #61dafb;
}
.icon3 {
  height: 25px;
  margin: 0 2.5px 0 2.5px;
  color: var(--footercolor);
  transition: color 0.2s ease-in-out;
}
.icon3:hover {
  color: white;
}
#fclass {
  display: flex;
  align-items: center;
  color: var(--footercolor);
}
#footer {
  position: absolute;
  bottom: 20%;
  left: 0;
  right: 0;
  color: var(--footercolor);
  height: 6vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
