body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  background: radial-gradient(rgb(3, 3, 194), rgb(4, 4, 73));
}
* {
  box-sizing: border-box;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
#drum-machine {
  width: 47vw;
  max-width: 400px;
  min-width: 250px;
  height: 50vh;
  margin: auto;
  margin-top: 20vh;
  background: radial-gradient(antiquewhite, rgb(242, 170, 99));
  border-radius: 10px;
  border: 1px solid black;
  box-shadow: 1px 1px 1px 1px navy;
}
#display {
  height: 10%;
  width: 80%;
  margin: auto;
  margin-top: 5%;
  margin-bottom: 5%;
  background-color: rgb(4, 25, 100);
  color: rgb(255, 91, 49);
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Times New Roman", Times, serif;
  font-size: larger;
  border-radius: 5px;
  border: 1px solid black;
}
#keyarea {
  margin: auto;
  display: flex;
  align-items: center;
  width: 95%;
  height: 75%;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 10px;
}
.drum-pad {
  width: 30%;
  height: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid black;
  box-shadow: 2px 2px black;
  border-radius: 10px;
  user-select: none;
  font-family: "Courier New", Courier, monospace;
  font-weight: 800;
  font-size: larger;
}
.drum-pad:hover {
  cursor: pointer;
}
#root2 {
  height: 100vh;
}
